<template>
<div>
  <div class="Select" >
    <p @click="selectClick">{{selectName}}<span :class="'vantIcon '+selectIcon"><van-icon name="arrow-down" /></span></p>
  </div>
  <div class="selectList" v-if="selectShow">
    <ul  >
      <li v-for="(item,index) in selectList" :class="index==itemId?'actives':''"  @click="liClick(item,index)" >{{item.name}}</li>
    </ul>
  </div>
</div>
</template>

<script>
  export default {
      components:{
      },
        data() {
          return {
            selectShow:false,
            selectIcon:false,
            selectName:"门店名称",
            itemId:"-2",
            selectList:[
              {name:"XXXXX门店名称1XXXXX门店名称1"},
              {name:"XXXXX门店名称1XXXXX门店名称2"},
              {name:"XXXXX门店名称1XXXXX门店名称3"},
              {name:"XXXXX门店名称1XXXXX门店名称4"},
              {name:"XXXXX门店名称1XXXXX门店名称5"},
              {name:"XXXXX门店名称1XXXXX门店名称6"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称"},
              {name:"XXXXX门店名称1XXXXX门店名称555"},
              {name:"XXXXX门店名称1XXXXX门店名称444"},
              {name:"XXXXX门店名称1XXXXX门店名称333"},
              {name:"XXXXX门店名称1XXXXX门店名称222"},
            ]
          }
        },
        methods:{
          storeList(){
            this.http.get2("store/stat/accountStoreList",{},this.$store.state.token)
              .then(res => {
                if(res.data.resultCode==1){
                  this.selectList=res.data.data;
                }else{
                  this.selectList=[];
                  Dialog.alert({
                    message:"获取门店数据失败！"
                  }).then(() => {
                  });
                }
              })
          },
          selectClick(){
            if(this.selectShow){
              this.selectIcon="iconbto";
              this.selectShow=false;
            }else{
              this.selectIcon="iconTop";
              this.selectShow=true;
            }
          },
          liClick(data,index){
            this.selectClick();
            this.selectName=data.name;
            this.itemId=index;
          }
        },
      }
</script>

<style scoped  lang="scss">
  .selectList{
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    position: fixed;
    left: 0;
    top: 182px;
    background:rgba(0,0,0,0.5);
    height: 1140px;
    /*overflow: hidden;*/
    overflow: scroll;
    padding-bottom: 100px;
    width: 100%;
    z-index: 99999999;
  ul{
    padding: 0  40px;
    background: #ffffff;
    width: 100%;
    box-sizing: border-box;
    padding-bottom: 100px;
  li{
    padding:32px 0;
    width: 100%;
    box-sizing: border-box;
    font-size:26px;
    font-family:PingFangSC;
    font-weight:400;
    color:rgba(72,72,72,1);
    line-height:36px;
    text-align: left;
  }
  .actives{
    color: #FF8D59;
  }
  }
  }
  .Select{
    width: 100%;
    line-height: 96px;
    font-size:32px;
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    position: fixed;
    z-index: 999999999;
    left: 0;
    top: 86px;
    border-top:2px solid #F0F0F0 ;
    box-shadow:0px 2px 2px 0px  rgba(0,0,0,0.08);
  p{
    background: #ffffff;
    width: 100%;
    height:96px;
    text-align: left;
    padding: 0 100px 0 36px;
    box-sizing: border-box;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
  }
  .vantIcon{
    position: absolute;
    right: 36px;
    top: 4px;
  i{
    font-size: 32px;
    color: #333333;
    font-weight:600;
  }
  }
  .vantIcon.iconTop{
    transform: rotate(180deg) ;
    transition: transform 0.3s;
  }
  .vantIcon.iconbto{
    transform: rotate(0deg) ;
    transition: transform 0.3s;
  }
  }
</style>



